import React, { useState } from "react"
import TextField from "@material-ui/core/TextField"
import MenuItem from "@material-ui/core/MenuItem"
import { useOptions } from "../../../../context/options"

const VoyageSelect = (props) => {
    const options_context = useOptions()
    const scheduleOptions = options_context.scheduleOptions
    const [selectedVoyage, setSelectedVoyage] = useState("")

    const handleVoyageSelection = (value) => {
        setSelectedVoyage(value)

        if (props.setScheduleId) {
            props.setScheduleId(value)
        }

        if (props.setVoyageNumber) {

            let schedules = options_context.shipSchedules

            let voyageNumber = schedules.filter((v) => {
                return v.id === value;
            });

            props.setVoyageNumber(voyageNumber[0].voyage)
        }

        if (props.setVoyageRoute) {
            let schedules = options_context.shipSchedules

            let schedule = schedules.filter((v) => {
                return v.id === value;
            });

            props.setVoyageRoute(`${schedule[0].fromLocation} - ${schedule[0].toLocation}`);
        }

    }

    return (
        <div>
            <TextField
                select
                label="Select Voyage #"
                name="voyage"
                value={props.selectedVoyage || selectedVoyage}
                onChange={(e) => handleVoyageSelection(e.target.value)}
                fullWidth
                required
                disabled={props.disabled || null}
                className="text-left mb-1"
            >
                {scheduleOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>
        </div>
    )
}

export default VoyageSelect
