import React, { useState } from "react"
import { useOptions } from "../../../../context/options"
import TextField from "@material-ui/core/TextField"
import MenuItem from "@material-ui/core/MenuItem"

const CompanySelect = (props) => {
  const companyOptions = useOptions().companyOptions
  const [companySelect, setCompanySelect] = useState(props.companySelect || "")

  const handleChange = (value) => {
    setCompanySelect(value)

    if (props.setCarrierId) {
      props.setCarrierId(value)
    }

    if (props.setCarrierName) {

      let label = companyOptions.filter((v) => {
        return v.value === value;
      });

      props.setCarrierName(label[0].label);
    }

    if (props.getShips) {
      props.getShips(value)
    }

  }

  return (
    <div>
      <TextField
        select
        label="Select Company"
        name="companySelect"
        value={props.companySelect || companySelect}
        onChange={(e) => handleChange(e.target.value)}
        fullWidth
        required
      >
        {companyOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
    </div>
  )
}

export default CompanySelect
