import {
  faCalendarDay,
  faUsers,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap";
import Passengers from "../../Dashboard/Passengers/Passengers";
import "./dashboardtab.css";

export default function DashboardTab(props) {
  const [activeTab, setActiveTab] = useState("1");

  const handleTabChange = (value) => {
    setActiveTab(value);
  };

  return (
    <div className="mt-5 pt-0 mt-0 dashboardTab">
      <Nav tabs>        
        <NavItem>
          <NavLink
            className={
              activeTab === "1" ? "active activeTabHeader" : "inactiveTab"
            }
            onClick={() => handleTabChange("1")}
          >
            <FontAwesomeIcon icon={faUsers} className="tabIcon" /> Passengers
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={activeTab} className="bg-white m-0 p-0 pl-2 pr-2 pb-2">
        <TabPane tabId="1" className="m-0 p-0"><Passengers /></TabPane>
      </TabContent>
    </div>
  );
}
