import React, { useState, useEffect, useCallback } from "react"
import { useAuth } from "../../../context/auth"
import { ProfileContext } from "../../../context/profile"
import API from "../../../utils/Api"
import { debounce, isArray } from "lodash"
import { Container, Row, Col } from "reactstrap"
import DataTable from "../../UI/Datatable/Datatable"
import Axios from "axios"
import DateFnsUtils from "@date-io/date-fns"
import { MuiThemeProvider, createMuiTheme, Tooltip, IconButton } from "@material-ui/core"
import TodayIcon from '@material-ui/icons/Today'
import MyDatePicker from "../../UI/Forms/Fields/MyDatePicker"
import { Checkbox } from "@material-ui/core"

const Passengers = (props) => {
  const token = useAuth().authTokens
  const dateFns = new DateFnsUtils()
  const [passengerData, setPassengerData] = useState([])
  const [currentPage, setCurrentPage] = useState()
  const [isLoading, setIsLoading] = useState(false)

  //toggle
  const [targetDate, setTargetDate] = useState(Date.now())
  const [isShowDatePicker, setIsShowDatePicker] = useState(false)
  const toggleDatePicker = () => setIsShowDatePicker(!isShowDatePicker)
  const handleDateChange = (d) => {
    setTargetDate(d)
    loadPassengers(d)
  }

  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableToolbar: {
          actions: {
            display: "flex",
            flex: "initial",
            // move all icons to the right
            "& > span, & > button": {
              order: 99
            },
            // target the custom toolbar icon
            "& > span:last-child, & > button:last-child": {
              order: 1
            },
            // target any icon
            "& > span:nth-child(4), & > button:nth-child(4)": {
              order: 2
            }
          }
        }
      }
    })

  const formatData = (data = null) => {
    if (data === null) {
      return null
    }

    if (!isArray(data)) {
      return formatObjectData(data)
    }

    return false
  }

  const formatObjectData = (data) => {
    let person = []
    let index = 0

    for (const [key, value] of Object.entries(data)) {
      
      let profile = value

      person[index] = [
        profile.id,
        `${profile.first_name.toUpperCase()} ${profile.last_name.toUpperCase()}`,       
        profile.origin.toUpperCase() || 'N/A',
        profile.destination.toUpperCase() || 'N/A',
        dateFns.format(new Date(profile.departure), "M/d/yyyy"),
        dateFns.format(new Date(profile.arrival), "M/d/yyyy"),
        profile.status.toUpperCase(),
      ]

      index++
    }
    return person
  }

  const loadPassengers = useCallback((dd = null) => {
    setIsLoading(true)
    let cancelToken = Axios.cancelToken
    let d
    let start, end

    if (dd === null) {
      d = Date.now()
    } else {
      d = dd
    }

    start = dateFns.format(new Date(d), "Y-M-dd 00:00:00")
    end = dateFns.format(new Date(d), "Y-M-dd 23:59:59")


    API.post(
      "lgu/lsi", {
      dates: [start, end],
      isDownload: false
    },
      {
        cancelToken: cancelToken,
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    )
      .then((result) => {
        let data = result.data.data
        let currentPage = result.data.current_page

        if (Object.keys(data).length > 0) {
          let passengers = formatData(data)
          setCurrentPage(currentPage)
          setPassengerData(passengers)
        }else{
          setPassengerData([])
        }

        setIsLoading(false)
      })
      .catch((e) => {
        setIsLoading(false)
      })
  }, [])

  const appendProfile = (page) => {

    setIsLoading(true)
    let cancelToken = Axios.cancelToken
    let start, end


    start = dateFns.format(new Date(targetDate), "Y-M-dd 00:00:00")
    end = dateFns.format(new Date(targetDate), "Y-M-dd 23:59:59")

    API.post(`lgu/lsi?page=${page}`, {
      dates: [start, end],
      isDownload: false
    }, {
      cancelToken: cancelToken,
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let data = result.data.data

      let newPassengerProfiles = []
      let passengers = formatData(data)

      if (result.data.current_page > currentPage) {
        setCurrentPage(result.data.current_page)
        if (passengers !== undefined) {
          newPassengerProfiles = [...passengerData, ...passengers]
        } else {
          newPassengerProfiles = passengerData
        }
      }

      setPassengerData(newPassengerProfiles)
      setIsLoading(false)
    })
  }

  const debounceSearchHandler = debounce(
    (queryData) => searchProfile(queryData),
    500
  )

  const searchProfile = (query) => {
    let cancelToken = Axios.cancelToken
    return API.post(
      `lgu/search`,
      {
        _text: query,
      },
      {
        cancelToken: cancelToken,
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    ).then((result) => {
      let data = result.data
      let passengers = formatData(data)
      setPassengerData(passengers)
      setIsLoading(false)
    })
  }

  const columns = [
    "Ref #",
    "Name",    
    "Origin",
    "Destination",
    "ETD",
    "ETA",
    "Status",
  ]

  const calendarButton = () => (
    <Tooltip disableFocusListener title="Filter Date">
      <IconButton onClick={() => toggleDatePicker()}>
        <TodayIcon />
      </IconButton>
    </Tooltip>
  )


  const options = {
    customToolbar: calendarButton,
    fixHeader: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "370px",
    selectableRows: "none",
    viewColumns: false,
    filter: true,
    elevation: 0,
    print: false,
    download: false,
    rowsPerPage: 15,
    rowsPerPageOptions: [15, 30, 100],
    onChangePage: (p) => {
      if (p % 2 !== 0) {
        if (p >= currentPage) {
          appendProfile(currentPage + 1)
        }
      }
    },
    onSearchClose: () => {
      setIsLoading(false)
      loadPassengers()
    },
    searchProps: {
      onKeyUp: (e) => {
        let queryString = e.target.value
        if (queryString.length >= 2) {
          setIsLoading(true)
          debounceSearchHandler(queryString)
        }
      },
    },
    // this is to disable auto search on local data
    customSearch: (query, row, col) => {
      return passengerData.length > 0
    },
  }

  useEffect(() => {
    loadPassengers()
    return () => { }
  }, [loadPassengers])

  return (
    <ProfileContext.Provider
      value={{ passengerData, loadPassengers }}
    >
      <Container fluid="true" className="p-0 m-0 ">
        <Row className="m-0 p-0 text-right">
          <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
            <MuiThemeProvider theme={getMuiTheme()}>
              <DataTable
                title="Passengers"
                loadPassengers={loadPassengers}
                data={passengerData}
                isLoading={isLoading}
                columns={columns}
                options={options}
              />
            </MuiThemeProvider>
          </Col>
        </Row>
      </Container>
      {
        isShowDatePicker && (
          <MyDatePicker isOpen={isShowDatePicker} toggleDatePicker={toggleDatePicker} handleDateChange={handleDateChange} />
        )
      }
    </ProfileContext.Provider>
  )
}

export default Passengers
