import React, { useEffect, useState } from "react"
import API from "../../../utils/Api"
import DateFnsUtils from "@date-io/date-fns"
import { useProfile } from "../../../context/profile"
import { useOptions } from "../../../context/options"
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
} from "reactstrap"

import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers"
import CheckBox from "@material-ui/core/Checkbox"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {
  faDownload,
  faFileExcel,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons"
import { useAuth } from "../../../context/auth"
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel"
import Info from "../Info/Info"
import { FormControlLabel, Paper } from "@material-ui/core"
import { generateManifestsExcel } from "../../../utils/excel_manifest"
import { Typography } from "@material-ui/core"
import CompanySelect from "../Forms/Fields/CompanySelect"
import ShipNameSelect from "../Forms/Fields/ShipNameSelect"
import VoyageSelect from "../Forms/Fields/VoyageSelect"

const Report = (props) => {
  const token = useAuth().authTokens
  const dateFns = new DateFnsUtils()
  const profile_context = useProfile()
  const options_context = useOptions()

  const [isProcessing, setIsProcessing] = useState(false)
  const [isError, setIsError] = useState(false)
  const [errorMessage, setErrorMessage] = useState("No Records Found")

  // checkboxes
  const [isDaily, setIsDaily] = useState(false)
  const [isMonthly, setIsMonthly] = useState(false)
  const [isCustomRange, setIsCustomRange] = useState(false)

  // value
  const [startDate, setStartDate] = useState(null)
  const [endDate, setEndDate] = useState(null)
  const [daily, setDaily] = useState(null)
  const [monthly, setMonthly] = useState(null)
  const [isStartDateSelected, setIsStartDateSelected] = useState(false)
  const [reportType, setReportType] = useState("Daily")
  const [isDownload, setIsDownload] = useState(false)
  const [isShowShips, setIsShowShips] = useState(true)
  const [isShowVoyage, setIsShowVoyage] = useState(true)

  // for reports
  const [shipLabel, setShiplabel] = useState("")
  const [shipId, setShipId] = useState("")
  const [carrierId, setCarrierId] = useState("")
  const [carrierName, setCarrierName] = useState("")
  const [scheduleId, setScheduleId] = useState("")
  const [voyageNumber, setVoyageNumber] = useState("")
  const [voyageRoute, setVoyageRoute] = useState("")

  const toggleReportType = (value, type) => {
    const reportTypeOptions = {
      daily: 1,
      monthly: 2,
      custom: 3,
    }

    setIsError(false)
    setIsDownload(value)
    setIsDaily(false)
    setIsMonthly(false)
    setIsCustomRange(false)

    switch (type) {
      case reportTypeOptions.daily:
        setReportType("Daily")
        setIsDaily(!isDaily)
        break
      case reportTypeOptions.monthly:
        setReportType("Monthly")
        setIsMonthly(!isMonthly)
        break
      default:
        setReportType("Custom")
        setIsCustomRange(!isCustomRange)
        break
    }
  }

  const setStartDateChangeHandler = (d) => {
    setStartDate(dateFns.format(new Date(d), "Y-M-dd"))
    if (d !== "") {
      setIsStartDateSelected(true)
    } else {
      setIsStartDateSelected(false)
    }
  }

  const setEndDateChangeHandler = (d) => {
    setEndDate(dateFns.format(new Date(d), "Y-M-dd"))
  }

  const generateReportHandler = (e) => {
    let start = ""
    let end = ""

    e.preventDefault()
    setIsError(false)
    setIsProcessing(true)
    try {
      if (isDaily) {
        // Format date for query
        start = dateFns.format(new Date(daily), "Y-M-dd 00:00:00")
        end = dateFns.format(new Date(daily), "Y-M-dd 23:59:59")
      }

      if (isMonthly) {
        // Format date for query
        start = dateFns.format(
          dateFns.startOfMonth(new Date(monthly)),
          "Y-M-dd 00:00:00"
        )
        end = dateFns.format(
          dateFns.endOfMonth(new Date(monthly)),
          "Y-M-dd 23:59:59"
        )
      }

      if (isCustomRange) {
        start = startDate
        end = endDate
      }

      getRecords(start, end)
        .then((result) => {
          if (Object.keys(result.data).length > 0) {
            try {
              let parsedRecords = parseData(result)
              let params = {
                records: parsedRecords,
                dates: [start, end],
                reportType: reportType,
                shipName: shipLabel,
                isDaily: isDaily,
                carrierId: carrierId,
                carrierName: carrierName,
                voyageRoute: voyageRoute,
                voyageNumber: voyageNumber,
              }

              generateManifestsExcel(params)
            } catch (e) {
              setIsError(true)
              setErrorMessage("Template not found")
            }

            setIsProcessing(false)
          } else {
            setIsProcessing(false)
            setErrorMessage("No Records Found")
            setIsError(true)
          }
        })
        .catch((e) => {
          setErrorMessage("Unable to access records")
          setIsError(true)
          setIsProcessing(false)
        })
    } catch (e) {
      setErrorMessage("Code Error")
      setIsError(true)
      setIsProcessing(false)
    }
  }

  const getRecords = (start, end) => {
    return API.post(
      "lgu/lsi",
      { dates: [start, end], ships: [shipId], schedules: [scheduleId], isDownload: true },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    )
  }

  const parseData = (result) => {
    let data = result.data
    let records = []

    records = data.map((v, i) => {
      return [
        v.first_name !== null ? v.first_name : "N/A",
        v.last_name !== null ? v.last_name : "N/A",
        v.companions.join(),
        v.accomodation !== null ? v.accomodation : "N/A",
        v.gender !== null ? v.gender : "N/A",
        v.age !== null ? v.age : "N/A",
        v.address !== null ? v.address : "N/A",
        v.zipcode !== null ? v.zipcode : "N/A",
        v.origin !== null ? v.origin : "N/A",
        v.destination !== null ? v.destination : "N/A",
        v.contact !== null ? v.contact : "N/A",
        v.email !== null ? v.email : "N/A",
        v.relative_name !== null ? v.relative_name : "N/A",
        v.relative_contact !== null ? v.relative_contact : "N/A",
        v.body_temp !== null ? v.body_temp : "N/A",
        v.seat_no !== null ? v.seat_no : "N/A",
        v.parking_space !== null ? v.parking_space : "N/A",
        v.place_of_travel !== null ? v.place_of_travel : "N/A",
      ]
    })

    return records
  }



  const getShips = (companyId) => {
    API.get(`lgu/ships/${companyId}`, {
      headers: {
        Authorization: "bearer " + token,
      },
    }).then((result) => {

      let data = result.data

      if (data.length > 0) {
        let options = data.map((v, i) => {
          return {
            value: v.id,
            label: v.name
          }
        })

        options_context.setShipOptions(options)
      } else {
        options_context.setShipOptions([])
      }

      setIsShowShips(false)
    })
  }

  const getShipSchedules = (shipId) => {
    API.get(`lgu/ships/schedule/${shipId}`, {
      headers: {
        Authorization: "bearer " + token,
      },
    }).then((result) => {

      let data = result.data

      if (data.length > 0) {

        options_context.setShipSchedules(data)

        let options = data.map((v, i) => {
          return {
            value: v.id,
            label: v.voyage
          }
        })

        options_context.setScheduleOptions(options)
      } else {
        options_context.setScheduleOptions([])
      }

      setIsShowVoyage(false)
    })
  }

  useEffect(() => {
    let carrier = profile_context.profileDetails
    setCarrierName(carrier.carrier_name)
    return () => { }
  }, [])

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-md mt-5 pt-5"
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faFileExcel} className="text-success mr-1" />
          Passenger Manifest Report
        </ModalHeader>
        <form onSubmit={(e) => generateReportHandler(e)}>
          <ModalBody>
            {isError && (
              <Col md={12} className="text-center m-0 p-0">
                <Typography variant="h6">
                  <Info className="p-0 m-0">{errorMessage}</Info>
                </Typography>
              </Col>
            )}
            <Row className="p-2">
              <Col md={12}>
                <Row>
                  <Col md={12}>
                    <Row className="mb-2">
                      <Col md={12}>
                        <Typography variant="button">
                          Ship Details
                        </Typography>
                        <Row>
                          <Col md={12}>
                            <CompanySelect getShips={(companyId) => getShips(companyId)} setCarrierId={setCarrierId} setCarrierName={setCarrierName} />
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12} className="mt-1">
                            <ShipNameSelect disabled={isShowShips} getShipSchedules={getShipSchedules} setShipId={setShipId} setShiplabel={setShiplabel} />
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12} className="mt-1 mb-1">
                            <VoyageSelect disabled={isShowVoyage} setScheduleId={setScheduleId} setVoyageNumber={setVoyageNumber} setVoyageRoute={setVoyageRoute} />
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12}>
                        <Paper className="p-2" elevation={1}>
                          <Typography variant="button" className="ml-1">
                            Report Types
                          </Typography>
                          <Row>
                            <Col md={{ size: "auto" }} className="m-0 p-0">
                              <FormControlLabel
                                className="mt-1 ml-3 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isDaily}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 1)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Daily"
                              />
                            </Col>
                            <Col
                              md={{ size: "auto" }}
                              className="m-0 p-0 text-left"
                            >
                              <FormControlLabel
                                className="mt-1 ml-1 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isMonthly}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 2)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Monthly"
                              />
                            </Col>
                            <Col md={{ size: "auto" }} className="m-0 p-0">
                              <FormControlLabel
                                className="mt-1 ml-1 p-0"
                                control={
                                  <CheckBox
                                    className="m-0 p-0 pr-1"
                                    checked={isCustomRange}
                                    onChange={(e) =>
                                      toggleReportType(e.target.checked, 3)
                                    }
                                    color="primary"
                                    size="small"
                                  />
                                }
                                label="Custom Range"
                              />
                            </Col>
                          </Row>
                        </Paper>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row className="mt-1">
                  <Col md={12} className="text-center pt-0">
                    {isDaily && (
                      <Col md={12} className="p-2 pt-0 mt-0">
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                          <KeyboardDatePicker
                            name="daily"
                            value={daily}
                            onChange={(d, v) => setDaily(d)}
                            label="Select Date"
                            format="MMM/d/yyyy"
                            required
                            clearable
                            hideTabs
                            showTodayButton
                            fullWidth
                          />
                        </MuiPickersUtilsProvider>
                      </Col>
                    )}
                    {isMonthly && (
                      <Col md={12} className="p-2 pt-0 mt-0">
                        <MuiPickersUtilsProvider
                          className="m-0 p-0"
                          utils={DateFnsUtils}
                        >
                          <KeyboardDatePicker
                            name="monthly"
                            className="m-0 p-0"
                            variant="dialog"
                            value={monthly}
                            onChange={(d, v) => setMonthly(d)}
                            label="Select Month"
                            format="MMMM yyyy"
                            views={["year", "month"]}
                            openTo="month"
                            required
                            clearable
                            hideTabs
                            fullWidth
                          />
                        </MuiPickersUtilsProvider>
                      </Col>
                    )}
                    {isCustomRange && (
                      <div>
                        <Col md={12} className="p-2 pt-0 mt-0">
                          <MuiPickersUtilsProvider
                            className="float-left"
                            utils={DateFnsUtils}
                          >
                            <KeyboardDatePicker
                              name="startDate"
                              value={startDate}
                              onChange={(d, v) => setStartDateChangeHandler(d)}
                              label="Start Date"
                              format="MMM/d/yyyy"
                              required
                              clearable
                              hideTabs
                              showTodayButton
                              fullWidth
                            />
                          </MuiPickersUtilsProvider>
                          <MuiPickersUtilsProvider
                            className="float-left mt-2"
                            utils={DateFnsUtils}
                          >
                            <KeyboardDatePicker
                              name="endDate"
                              value={endDate}
                              onChange={(d, v) => setEndDateChangeHandler(d)}
                              label="End Date"
                              minDate={dateFns.addDays(new Date(startDate), 1)}
                              format="MMM/d/yyyy"
                              disabled={!isStartDateSelected}
                              required
                              clearable
                              hideTabs
                              fullWidth
                            />
                          </MuiPickersUtilsProvider>
                        </Col>
                      </div>
                    )}
                  </Col>
                </Row>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Generating Report" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            {isDownload && (
              <Button
                type="submit"
                color="success"
                disabled={isProcessing}
                style={{ minWidth: "120px" }}
              >
                <FontAwesomeIcon icon={faDownload} /> Download
              </Button>
            )}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  )
}

export default Report
